import React from 'react';
import {Button, Header, Icon} from "semantic-ui-react";
import iconCircleSum from '../../../media/SVG/circles black and grey/SVG/2.svg'
import iconCircleDiff from '../../../media/SVG/circles black and grey/SVG/4.svg'
import iconCircleSymDiff from '../../../media/SVG/circles black and grey/SVG/3.svg'
import iconCircleUnion from '../../../media/SVG/circles black and grey/SVG/1.svg'
import {connect} from "react-redux";
import {
    deleteFromCluster, deleteSelectedCluster,
    downloadWorkSpace, executeClusterAction, getFriendsAction,
    intersectClusters, pickOutToCluster,
    subtractClusters, symDiffClusters,
    unionClusters,
    uploadWorkSpace
} from "../../../store/actions";
import {
    getActiveClusterID,
    getCurrentClusterData,
    getCurrentEntitiesActions,
    getEntitiesItemsIDS
} from "../../../store/selectors";
import ActiveActions from "./ActiveActions";


export const ActionPanelButton = (props) => {
    const style = {
        display: 'flex',
        alignItems: 'center',
    };
    const imgSize = '1.5rem';
    const iconStyle = {
        display: 'inline-block',
        marginRight: '5px',
        width: imgSize,
        height: 'auto'
    };
    const icon = typeof props.icon === 'object' ? props.icon : <img src={props.icon} alt={'afs'} style={iconStyle}/>;
    return (
        <Button basic onClick={props.onClick}>
            <div style={style}>
                <div style={{width: '1rem'}}>{icon}</div>
                <div style={{margin: 'auto'}}>{props.text}</div>
            </div>
        </Button>
    )
};


const ActionsPanel = (props) => {
    const headerMargin = '0.2rem';
    // console.log('ActionsPanel', props);
    return <div>
        <Header size={'small'} style={{marginLeft: headerMargin, marginTop: '1rem'}}></Header>
        <Button.Group vertical basic style={{width: '100%'}}>
            {
                props.haveClusters &&
                <ActionPanelButton onClick={props.downloadWorkSpace} text={'  '}
                                   icon={<Icon name={'download'}/>}/>
            }
            <ActionPanelButton onClick={props.uploadWorkSpace} text={'  '}
                               icon={<Icon name={'upload'}/>}/>
        </Button.Group>
        <ActiveActions header={true} headerMargin={headerMargin}/>
        {
            props.selectedEntitiesCount > 0 &&
            <>
                <Header size={'small'} style={{marginLeft: headerMargin}}> </Header>
                <Button.Group vertical basic style={{width: '100%'}}>
                    <ActionPanelButton onClick={props.pickOutToCluster} text={'   '}
                                       icon={<Icon name={'dot circle outline'}/>}/>
                    <ActionPanelButton onClick={props.deleteFromCluster} text={'  '}
                                       icon={<Icon name={'remove circle'}/>}/>
                    {
                        props.entitiesActions &&
                        props.entitiesActions.map(
                            action => (
                                <ActionPanelButton key={action.id} onClick={() => props.executeClusterAction(action)}
                                                   text={action.name}
                                                   icon={<Icon name={'circle outline'}/>}
                                />
                            )
                        )
                    }

                </Button.Group>
            </>
        }

        {
            props.selectedClustersCount > 0 &&
            <>
                <Header size={'small'} style={{marginLeft: headerMargin}}> </Header>
                <Button.Group basic compact vertical style={{width: '100%'}}>
                    {/*{*/}
                    {/*    props.selectedClustersCount === 2 &&*/}
                    {/*    <ActionPanelButton text={''} icon={<Icon name={'sun outline'}/>}/>*/}
                    {/*}*/}
                    {
                        props.selectedClustersCount >= 2 &&
                        <>
                            <ActionPanelButton onClick={props.intersectClusters} text={''}
                                               icon={iconCircleUnion}/>
                            <ActionPanelButton onClick={props.unionClusters} text={''} icon={iconCircleSum}/>
                            <ActionPanelButton onClick={props.subtractClusters} text={''}
                                               icon={iconCircleDiff}/>
                            <ActionPanelButton onClick={props.symDiffClusters} text={' '}
                                               icon={iconCircleSymDiff}/>
                        </>
                    }
                    <ActionPanelButton onClick={props.deleteSelectedClusters} text={' '}
                                       icon={<Icon name={'trash alternate outline'}/>}/>
                </Button.Group>
            </>
        }


    </div>
};

const mapStateToProps = state => {
    return {
        selectedClustersCount: state.workSpace.clusters.highlightedClusters.length,
        selectedEntitiesCount: state.workSpace.entities.selectedItems.length,
        haveClusters: state.workSpace.clusters.items.length > 0,
        currentClusterEntitiesCount: getEntitiesItemsIDS(state).length,
        clusterActions: getActiveClusterID(state) && getCurrentClusterData(state).actions,
        entitiesActions: getActiveClusterID(state) && getCurrentEntitiesActions(state),
    }
};
const mapDispatchToProps = {
    executeClusterAction,
    downloadWorkSpace,
    uploadWorkSpace,
    intersectClusters,
    unionClusters,
    subtractClusters,
    symDiffClusters,
    pickOutToCluster,
    deleteSelectedCluster,
    getFriendsAction,
    deleteFromCluster
};

export default connect(mapStateToProps, mapDispatchToProps)(ActionsPanel)