import {Button, Header, Icon} from "semantic-ui-react";
import React from "react";
import {ActionPanelButton} from "./ActionsPanel";
import {connect} from "react-redux";
import {executeClusterAction} from "../../../store/actions";
import {
    getActiveClusterID,
    getCurrentClusterData,
    getCurrentEntitiesActions,
    getEntitiesItemsIDS
} from "../../../store/selectors";

const ActiveActions = props => {
    let header = props.header;
    return <>
        {
            props.clusterActions.length > 0 &&
            <>{
                header &&
                <Header size={'small'} style={{marginLeft: props.headerMargin}}> </Header>

            }
                <Button.Group vertical basic style={{width: '100%', marginBottom: '1rem'}}>
                    {
                        props.clusterActions.map(action => (

                            <ActionPanelButton key={action.id}
                                               onClick={() => props.executeClusterAction(action)} text={action.name}
                                               icon={<Icon name={'object ungroup outline'}/>}/>
                        ))
                    }
                </Button.Group>
            </>
        }
    </>
};

const mapStateToProps = state => {
    return {
        selectedClustersCount: state.workSpace.clusters.highlightedClusters.length,
        selectedEntitiesCount: state.workSpace.entities.selectedItems.length,
        haveClusters: state.workSpace.clusters.items.length > 0,
        currentClusterEntitiesCount: getEntitiesItemsIDS(state).length,
        clusterActions: getActiveClusterID(state) && getCurrentClusterData(state).actions,
    }
};
const mapDispatchToProps = ({
    executeClusterAction
});

export default connect(mapStateToProps, mapDispatchToProps)(ActiveActions)